<template>
  <el-row type="flex">
    <el-dropdown trigger="click">
      <el-button>
        菜单
      </el-button>
      <el-dropdown-menu>
        <el-dropdown-item v-for="item in navList" :key="item.navItem">
          {{item.navItem}}
        </el-dropdown-item>
        <el-dropdown-item><span @click="exit">退出</span></el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>
  </el-row>
</template>

<script>
export default {
  name: 'NavMenu',
  data () {
    return {
      navList: [
        {name: '/index', navItem: '首页'},
        {name: '/jotter', navItem: '笔记本'},
        {name: '/library', navItem: '图书馆'},
        {name: '/person', navItem: '个人中心'}
      ]
    }
  },
  methods: {
    exit () {
      window.localStorage.removeItem('user')
      window.location.reload()
    }
  }
}
</script>

<style scoped>

</style>



