<template>
  <body>
    <el-row type="flex" class="top" justify="center">
      <el-col :lg="8">
        <el-form id="form" :model="loginForm">
          <h1>Login</h1>
          <br/>
          <el-form-item prop="username" :rules="[
            {required: true, message: '用户名不能为空'}
          ]">
            <el-input v-model="loginForm.username" placeholder="username">
              <template slot="prepend"><i class="el-icon-user-solid"></i></template>
            </el-input>
          </el-form-item>
          <el-form-item prop="password" :rules="[
            {required: true, message: '密码不能为空'}
          ]">
            <el-input show-password v-model="loginForm.password" placeholder="password">
              <template slot="prepend"><i class="el-icon-unlock"></i></template>
            </el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="login">登录</el-button>
            <el-button plain @click="register">注册</el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </body>
</template>

<script>
export default {
  name: 'Login',
  data () {
    return {
      loginForm: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    login () {
      this.$axios
        .post('/login', {
          username: this.loginForm.username,
          password: this.loginForm.password
        })
        .then(response => {
          console.log(response.data)
          this.$router.replace({path: '/index'})
        })
        .catch(error => {
          console.log(error)
          this.$store.commit('login', this.loginForm)
          this.$router.replace({path: '/index'})
        })
    },
    register () {
      this.$notify.warning({
        title: 'warning',
        message: '注册还没实装呢，直接随便输入就能登录了'
      })
    }
  }
}
</script>

<style scoped>
#form{
  margin-top: 100px;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 2px 12px rgba(0,0,0,0.2);
  background-color: rgba(255,255,255,0.4);
}
body{
  background-image: url("../assets/纳西妲背景.png");
  width: 100%;
  height: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: fixed;
}
</style>



